import React, {Component} from 'react';
import {Bar} from 'react-chartjs-2';
import {Container, Row, Col} from 'react-bootstrap';
import "chartjs-plugin-datalabels";

class Chart extends Component {

    constructor(props) {
        super(props);
        this.state = {
            chartData:props.chartData
        }
    }

    static defaultProps = {
        title: 'Title',
        displayTitle: true,
        displayLegend: true,
        legendPosition: 'right'
    }
    



    render () {
      return(
        
          <div className="chart">
                
            <Bar
            data={this.state.chartData}

            options={{

                responsive:true,
                title:{
                    display:true,
                    text: this.props.title,
                    fontSize: "20",
                },
                legend:{
                    display: false,
                    position: 'bottom',
                },
                scales: {
                    yAxes: [
                    {
                        ticks: {
                        stepSize: 20,
                        min: 0,
                        max: 100
                        },
                    }
                    ],
                    xAxes: [
                    {
                        gridLines: {
                            display: false
                        },
                    }
                    ]
                },
                tooltips: {
                    mode:'point',
                },
                plugins: {
                    datalabels: {
                        color: 'black',
                        anchor: "end",
                        align: "end",
                        labels: {
                            title: {
                                font: {
                                    size: "16"
                                }
                            },
                        }
                    }
                }
                
            }}
            /> 
            
          </div>
      )
    } 
}

export default Chart;