import React, { Component } from 'react';
import {Card} from 'react-bootstrap'

class Tile extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            textTile: props.textTile,
            subtitleTile: props.subtitleTile,
            valueTile: props.valueTile
         }
    }
    render() { 
        return ( 
            <div>
                <Card  border = 'light'  style={{ height:"20vh", width: "", color: "rgba(0,0,0, 1)", backgroundColor:"rgba(232,232,232,1)"}}>
                    <Card.Body>
                        <Card.Title style={{fontSize:"2vh", fontWeight:"bold"}}> {this.state.textTile}</Card.Title>
                        <Card.Subtitle style={{fontSize:"1.5vh"}}> {this.state.subtitleTile}</Card.Subtitle>
                        <Card.Text style= {{fontSize:"4vh", fontWeight:"bold"}}>
                            {this.state.valueTile}
                        </Card.Text>
                    </Card.Body>
                </Card>
            </div>
         );
    }
}
 
export default Tile;